<template lang="html">
  <el-row
  class="centent"
  type="flex"
  justify="center"
    >
    <el-col :xl="14">
      <h2>{{name}}</h2>
      <video class="video_box" :src="video_url()" controls>
      </video>
      <video-tool ref="videoTool" :id="id"></video-tool>
      <random-video ref="randomVideo"></random-video>
    </el-col>
  </el-row>
</template>

<script>
import videoTool from '../components/videoTool'
import randomVideo from '../components/randomVideo'
export default {
  props: [
    "id"
  ],
  data() {
    return {
      url: '',
      videoid: '',
      name:''
    }
  },
  methods: {
    video_url() {
      return '/video/play/' + this.id
    },
  },
  mounted() {
    if (!this.name) {
      this.$http.get('/video/name',{
        params:{
          id:this.id
        }
      }).then((res)=>{
        this.name = res.data
      })
    }
  },
  components:{
    videoTool,
    randomVideo
  }
}
</script>

<style lang="css">
.content{
  width: 100%;
}
.video_box{
  width: 100%
}
</style>



